    justify-content: center;
}
.home-buttons{
    display:flex;
    flex-direction: column;
    width:200px;
    height:400px;
    justify-content: space-around;
    margin-top:40px;
    margin-left:0px;
}

.home-h1 {
    font-size: 40px;
    width:70%;
    margin-left:auto;
    margin-right:auto;
    margin-top:0;
    color:black;
 }

 .home-h2{
  height:50px;
  font-size: 40px;
  margin-top:20px;
  margin-left:190px;
  width:250px;
  color:black;
 }



.guru-img1{
    -webkit-animation: mover 2s infinite  alternate;
    animation: mover 2s infinite  alternate;
    width:500px;
}
.answer-section{
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    margin-top:100px;
}
.speech-image{
    width:600px;
    margin-right: 80px;
    margin-bottom: 150px;
    position:absolute;
    left: -150px;
    /* margin-bottom: 150px; */
    top: 0;
}
.response-container{
    position:absolute;
    top:200px;
    left:550px;
}


.guru-img2{
    width:200px;
    margin-top:400px;
}


@-webkit-keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}

.question-section{
    display:flex;
    justify-content: center;
    margin-top:50px;
}
