}

.thisthat-answer{
    display:flex;
    flex-direction: column;
    height:700px;
    flex-wrap: wrap;
    width:1000;
  
}
.thisthat-answer button{
    margin-top:20px;
    width:150px;
}
.answer-input-container{
    margin-top:40px;
}

#answer-image{
    margin-top:350px;
    margin-left:50px;
    width:400px;
    margin-right: 0;
    
}
#bubble-answer{
   margin:0;
   width:600px;
}
/* // dropdown css starts */

.dropdown-container{
    width:200px;  
    background-color: white;
    height:40px;
}
  
.dropdown-btn{
    width:200px;
    height:40px;
    background-color: white;
}

.dropdown-list {
    list-style-type: none;
    position: absolute;
    margin: 0;
    padding: 0;
    width: 200px;
    height: 200px;
    border: none;
    background: white;
    box-shadow: 0 2px 4px 0 rgba(black, .5);
    z-index: 9;
    overflow: auto;
    border-radius: 3px;
}

.option {
    padding: 5px;
    cursor: pointer;
}
.option:hover {
    color: white;
    background: #4db8ff;
  }

.arrow {
    position: relative;
}
.arrow:after {
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 5px 0 5px;
      border-color: black transparent transparent transparent;
      position: absolute;
      right: 10px;
      top: 7px;
    }

    @media only screen and (max-width: 600px) {
        .home-buttons{
           visibility: hidden;
        }
        .hamburger-menu{
            visibility: visible;
        }
        .home{
         margin-left: auto;
         margin-right: auto;
         width: 50%;
        }

        .guru-img1{
           width:350px;
           margin-right:100px;
           margin-top:40px;
           }
           .home-h1 {
              font-size: 20px;
              width:80%;
              margin-top:50px;
            
           }
        
           .home-h2{
            height:50px;
            margin-top:40px;
            margin-left:100px;
            width:200px;
            font-size: 30px;
           }
      }



  

